const Test = () => {
  const time = new Date(19, 1, 1, 14).getHours();
  let greeting;
  if (time < 12) {
    greeting = "GoodMorning";
  } else if (time < 18) {
    greeting = "Afernoone";
  } else {
    greeting = "good Nigth";
  }
  return (
    <>
      <h1>Good {greeting}</h1>
    </>
  );
};

export default Test;
